import useAppSettings from '../../hooks/useAppSettings';

const CheckFromEmailSpamJunkWarning = ({text}: {text?: string | undefined}) => {
    const {fromEmailAddress, isLoading} = useAppSettings();

    const message = isLoading ? (
        <>
            Please check your <strong>Spam</strong> or <strong>Junk</strong>{' '}
            email folders to ensure delivery.
        </>
    ) : (
        <>
            Please add {fromEmailAddress} to your safe senders list to ensure
            delivery.
        </>
    );

    return (
        <p className="mb-3">
            {text && (
                <>
                    {text}
                    <br />
                </>
            )}
            {message}
            <br />
            If you do not receive an email please check your Spam or Junk email
            folders.
        </p>
    );
};

export default CheckFromEmailSpamJunkWarning;
